export default {
    findElementById(id){
        let index = this.elements.findIndex(e => e.id === id)
        let found = index !== -1;
        return {
            exist: found,
            index: index,
            element: this.elements[index]
        }
    },
    registerElement(id, el, options){
        let {exist} = this.findElementById(id).exist
        if(exist)
            return console.log(`id ${id} existed`)
        this.elements.push({
            id: id,
            el: el,
            options: options
        });
    },
    isOnOffset(id){
        return {
        }
    },
    updateElements(){
        this.elements.forEach(e => {
            if(typeof e.options.fn === "function")
                e.options.fn(e.el, {
                    id: e.id,
                    el: e.el,
                    scroll: {
                        x: this.x,
                        y: this.y
                    }
                })
        });
    },
    removeElement(id){
        let element = this.findElementById(id)
        if(!element.exist) return
        this.elements.splice(element.index, 1)
    }
}