import element_methods from './methods/element'
import offset_directive from './../directives/offset'

export default function(Vue){
    let app = new Vue({
        data: {
            x: 0,
            y: 0,

            //element
            elements: []
        },
        computed: {
            isTop(){
                return this.y === 0
            },
            isBottom(){
                return this.y === document.body.offsetHeight
            }
        },
        methods: {
            updateScroll: function(){
                this.x = window.scrollX;
                this.y = window.scrollY;
                this.updateElements()
            },
            //element compare
            ...element_methods
        },
        created(){
            this.updateScroll();
            window.addEventListener('scroll', this.updateScroll)
        },
        destroyed(){
            delete this.elements;
            window.addEventListener('scroll', this.updateScroll)
        }
    });
    Vue.prototype.$scroll = app;
    offset_directive(Vue)
}