export default (Vue)=>{
    Vue.directive("offset", {
        bind: (el, binding, vnode)=>{},
        inserted: (el, binding, vnode)=>{
            var id = `offset-${(new Date()).valueOf()}`
            el.setAttribute('offset-id', id);
            vnode.context.$root.$scroll.registerElement(id, el, {
                fn: (el, options)=>{
                    window.s = el
                    var e = el;
                    var offsetTop = e.offsetTop
                    while(e !== document.body){
                        e = e.offsetParent
                        offsetTop += e.offsetTop
                    }
                    if(options.scroll.y >= (offsetTop - (window.innerHeight - (window.innerHeight*.1)))){
                        el.style.opacity = 1;
                        el.classList.add(...binding.value);
                    } else {
                        el.style.opacity = 0;
                        el.classList.remove(...binding.value);
                    }
                }
            })
        },
        update: (el, binding, vnode)=>{},
        unbind: (el, binding, vnode)=>{},
    })
}