import anchormethods from './methods/anchor'
export default function(Vue){
    let app = new Vue({
        data: {
            /* 
                @anchors
                store anchor object 
            
                @structure
                {
                    @id: @Popper Instance
                }
            */
            anchors: {},

            /*
                @update {Array<Object>}
                array to store update object

                @key    {String}    - unique string
                @fn     {Function}  - require callback
            */
            updates: [],
        },
        methods: {

            // @updateall() : void
                // update all object stored at @update
            updateall(){
                this.updates.forEach(update => {
                    if(!update.lazy)
                        update.fn()
                })
            },

            // @loop() : void
                // request animation frame to render the element
                // alway
                // update depends on value of @loopstatus
            loop(){
                window.requestAnimationFrame( n =>{
                    if(this.loopstatus){
                        this.updateall();
                        this.loop();
                    }
                })
            },

            /*
                anchormethods

                @pushanchor(id {string}, el {object}, [ option {object}, lazy {boolean} ])
                @updateAnchor(id {string, reference {dom}, popper {dom}, [ lazy {boolean} ])
                @forceUpdateAnchor(id {string}, [ lazy {boolean} ])
                @getIndexUpdate(id {string}) {integer}
                @getUpdateAnchor(id {string}) {object}
                @removeAnchor(id {string})
            */
            ...anchormethods
        },
        created: function(){
            // set the @loopstatus to true so the loop can going
            this.loopstatus = true;
            this.loop();
        },
        destroyed: function(){
            // set the @loopstatus to false to stoped the looping
            this.loopstatus = false;

            // #todo: clear the object stored the instance
            //
        }
    });
    Vue.prototype.$layouts = app
}