import { createPopper } from '@popperjs/core'

export default {
    /* 
        @pushAnchor() : void
        store a new Instance Popper as new anchor to @anchors
        
        @param  id  {String}            - an unique string as id
        @param  el  {DOM-Object}        - an object that must contains @reference
                                          and @popper key that is DOM-Object as the type
        @param  options {Object}        - the object to set as Popper Options
                                          read @Popper/core.js documentation
        @param  lazy {Boolean}          - to set the anchor will update every miliseconds
                                          that always called when the @loop() run or set the @lazy true
                                          if the update is depending on the default event
                                          the default event is window resize, window scroll and some
                                          additional event on @updateAnchor()
    */
    pushAnchor(id, el, options = null, lazy = false){
        this.$nextTick(()=>{
            this.anchors[id] = createPopper( el.reference, el.popper, options)

            // if the instance Popper successful created then register the update popper
            if(this.anchors[id]) this.updateAnchor(id, el.reference, el.popper, lazy)
        })
    },
    
    /*
        @updateAnchor() : void
        register the update object store to @updates

        @param  id  {String}                - an unique id from registered anchor
        @param  reference   {DOM Object}    - an Dom object as reference element
        @param  popper  {DOM Object}        - an Dom object as popper element
        @param  lazy    {Boolean}           - boolean option for lazy update
    */
    updateAnchor(id, reference, popper, lazy = true){
        this.updates.push({ id: id, reference: reference, popper: popper, fn: ()=>{ this.forceUpdateAnchor(id) }, lazy: lazy })
        if(lazy){
            document.body.addEventListener("click", this.getUpdateAnchor(id).fn )
            popper.addEventListener("click", this.getUpdateAnchor(id).fn )
            reference.addEventListener("transitionend", this.getUpdateAnchor(id).fn )
        }
    },

    /*
        @forceUpdateAnchor() : Void
        method that called if we want to update the spesify object registered to @options
        
        @param     id : string             - the unique id
        @param     lazy : boolean          - option for lazy update
    */ 
    forceUpdateAnchor(id, lazy = null){
        if(this.anchors[id]){
            let update = this.getUpdateAnchor(id)
            if(update.reference && update.popper){
                if(typeof lazy === "boolean")
                    this.getUpdateAnchor(id).lazy = lazy
                this.anchors[id].forceUpdate()
            } else {
                this.removeAnchor(id)
            }
            // check if lazy is not {null}
        }
    },

    checkAnchor(id){
        return this.anchors[id] ? true : false;  
    },

    /* 
        @getindexupdateanchor : int
        method that return int index update of id parse

        @param  id : { String }             - the unique id
        @return { Integer }
    */
    getIndexUpdate(id){
        return this.updates.findIndex( u => u.id === id)
    },
    /* 
        @getAllIndexUpdateAnchor : int[]
        method that return array of int index update of id parse

        @param  id : { String }             - the unique id
        @return { Array }
    */
    getAllIndexUpdate(id){
        return this.updates.map((e,i)=>e.id==1 ? i : null).filter(e=>e != null)
    },

    /*
        @getUpdateAnchor : void
        get update object by id

        @param  id  { String }                  - the unique id
        @return { Object }                      - update object that stored from @updateAnchor()
    */
    getUpdateAnchor(id){
        let update = this.getIndexUpdate(id)
        return update === -1 ? null : this.updates[update]
    },
    /*  */
    getUpdateAnchors(id){
        var updates = this.updates.map((e,i)=>{ if(e.id==id) return e }).filters(e => e !== undefined);
        return updates
    },

    /* 
        @removeAnchor() : void
        destroy the object and unregister from @update

        @param  id  { String }              - the unique id
    */
    removeAnchor(id){
        let update = this.getUpdateAnchor(id)
        if(update === null) return

        if(update.lazy){
            try {
                document.body.removeEventListener("click", update.fn )
                update.popper.removeEventListener("click", update.fn )
                update.reference.removeEventListener("transitionend", update.fn )
            } catch (error) {
                
            }
        }
        this.updates.splice(this.getIndexUpdate(id), 1)
        delete this.anchors[id]
    },
}